@font-face {
  font-family: "muller";
  src: url("../../../../font/Fontfabric - MullerRegular.otf");
}
.full-post .modal-content {
  font-family: muller;
  padding: 27px 32px;
  border-radius: 20px;
  width: 100%;
}

.full-post__text {
  margin-top: 14px;
  margin-bottom: 1px;
  width: 380px;
  height: 162px;
  overflow: auto;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 170%;
  color: #424141;
  display: flex;
  align-items: center;
}

.full-post__images {
  display: flex;
  justify-content: space-around;
}

.full-post__image {
  display: flex;
  justify-content: center;
  width: 172px;
  height: 149px;
  background: #F8F8F8;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  border-radius: 6px;
}

.full-post__photo {
  width: 172px;
  height: 149px;
}

.full-post__icon {
  width: 30px;
}

.full-post__email {
  margin-top: 11px;
  margin-bottom: 32px;
  width: 140px;
  height: 14px;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 100%;
  color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  text-transform: capitalize;
}

.full-post__interested-btn {
  margin-top: 19px;
}

.full-post__exit {
  margin-left: 96%;
  margin-bottom: 25px;
}
.full-post__exit:hover {
  cursor: pointer;
}

.full-post__exit-img {
  width: 50px;
}

.full-post__avatar {
  vertical-align: top;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  margin-right: 11px;
}

.full-post_avatar-empty {
  margin-right: 11px;
  align-self: center;
  width: 65px;
  height: 65px;
  padding: 5px;
  background-color: #666666;
  border-radius: 50%;
  filter: invert(66%) sepia(0%) saturate(609%) hue-rotate(187deg) brightness(107%) contrast(98%);
}

.full-post__top-list {
  display: flex;
  padding: 0 0 9px 0;
  list-style: none;
  margin: 0;
}

.full-post__user-name {
  font-size: 28px;
  font-style: normal;
  font-weight: bold;
  line-height: 28px;
  color: #000000;
  margin: 0;
}

.full-post__package-provider {
  font-family: muller;
  position: absolute;
  right: 5%;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  color: #FD5A01;
}

.full-post__address {
  margin-top: 10px;
  display: inline-block;
  width: 30%;
}

.full-post__details {
  display: flex;
  align-content: flex-end;
  height: 60px;
}