.reviews {
  margin-top: 3%;
  display: flex;
  overflow: auto;
  flex-flow: row wrap;
  padding-left: 7%;
}
.reviews::-webkit-scrollbar {
  width: 5px;
  box-sizing: content-box;
}
.reviews::-webkit-scrollbar-track {
  background: #F4F3F3;
  border-radius: 10px;
}
.reviews::-webkit-scrollbar-thumb {
  background: #D8D8D8;
  border-radius: 10px;
}
.reviews::-webkit-scrollbar-thumb:hover {
  background: #888;
}

.reviews__user-profile {
  height: 450px;
}

.reviews__message {
  color: gray;
  margin: auto;
  padding: 4%;
}

.reviews__profile {
  height: 110px;
}

.review {
  padding: 5px 9px;
  width: 154px;
  height: 101px;
  margin-right: 3%;
  margin-bottom: 2%;
  border-radius: 10px;
}

.review-orange {
  background-color: #FD5A01;
}

.review-blue {
  background-color: #202A49;
}

.review__rating-star {
  margin-top: 0;
  padding-top: 0;
  margin-right: 3px;
  width: 11px;
  filter: invert(100%) sepia(0%) saturate(7471%) hue-rotate(26deg) brightness(100%) contrast(97%);
}

.review__text {
  overflow: hidden;
  height: 38px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 130%;
  display: flex;
  align-items: flex-start;
  color: #FFFFFF;
}

.review__time {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 150%;
  display: flex;
  align-items: center;
  color: rgba(255, 255, 255, 0.8);
  margin-top: 2px;
}