.header-landing {
  position: relative;
  padding-left: 5%;
  padding-right: 5%;
  z-index: 45754;
}

@media screen and (max-width: 767px) {
  .nav {
    flex-flow: column;
  }
}

@media screen and (max-width: 767px) {
  ul.mr-auto.landing__navbar.nav {
    margin-left: 0 !important;
    justify-self: flex-start;
  }
}

.landing__nav-item {
  position: relative;
  z-index: 1;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 18px;
  color: #FFFFFF;
}
.landing__nav-item:hover {
  text-decoration: none;
  color: #FFFFFF;
}

.landing__scroll-btn {
  background: transparent;
  outline: none;
  border: none;
  color: #FFFFFF;
}
.landing__scroll-btn:focus {
  outline: none;
}

.landing__navbar {
  margin-right: 0 !important;
  margin-left: auto;
  display: flex;
  align-items: center;
}

.nav__login {
  z-index: 1;
  background: transparent;
  outline: none;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  padding: 12px 20px;
}
.nav__login:focus {
  outline: none;
}
@media screen and (max-width: 768px) {
  .nav__login {
    border: none;
  }
}