.footer {
  padding-left: 30%;
  display: flex;
  align-items: center;
  background-color: #202A49;
  grid-column-start: 1;
  grid-column-end: 3;
}

.footer__link {
  display: inline;
  margin-right: 20px;
  color: #FFFFFF;
  border: none;
  outline: none;
  background: none;
}
.footer__link:focus {
  border: none;
  outline: none;
}

.footer__faq {
  margin-left: auto;
}